					            <a role="menuitem" tabindex="-1" href="#" style="line-height: 2em;"><img src="../../assets/images/mobile/helpicon.png" style="width: 1em;margin-right: 0.5em;"/></a>
					        </li>
					        <li role="presentation">
					            <a role="menuitem" tabindex="-1" href="#" style="line-height: 2em;"><img src="../../assets/images/mobile/messageicon.png" style="width: 1em;margin-right: 0.5em;"/></a>
					        </li>
					        <li role="presentation">
					            <a role="menuitem" tabindex="-1" href="#" style="line-height: 2em;"><img src="../../assets/images/mobile/logouticon.png" style="width: 1em;margin-right: 0.5em;"/></a>
					        </li>
					    </ul>
					</div>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;padding: 0 15px;">
				<div class="col-sm-12 col-xs-12" style="padding: 0 15px;background: #e1eef5;border: 1px solid #00a6ff;">
					<p class="pull-left action" style="margin-bottom: 5px;">1</p>
					<div class="pull-left" style="width: 19%;height: 19px;border-bottom: 1px solid #999;margin: 0 5px;"></div>
					<p class="pull-left unaction" style="margin-bottom: 5px;">2</p>
					<div class="pull-left" style="width: 19%;height: 19px;border-bottom: 1px solid #999;margin: 0 5px;"></div>
					<p class="pull-left unaction" style="margin-bottom: 5px;">3</p>
					<div class="pull-left" style="width: 19%;height: 19px;border-bottom: 1px solid #999;margin: 0 5px;"></div>
					<p class="pull-left unaction" style="margin-bottom: 5px;">4</p>
				</div>
			</div>
			<div id="mobileStep1">
				<div class="row" style="margin-top: 1em;">
					<div class="col-sm-12 col-xs-12" style="padding: 0 15px;">
						<div class="row" style="background: #e1eef5;padding: 15px;">
							<div class="col-sm-12 col-xs-12">
								<div class="row">
									<p class="pull-left" style="font-size: 1.3em;font-weight: bold;"> {{userInfo.Rcoins}} R</p>
									<router-link class="pull-right" style="margin-top: 0.5em;" to="/RBrecharge">R</router-link>
								</div>
								<div class="row">
									<p class="pull-left" style="color: #999;">R1R = 1</p>
								</div>
							</div>
						</div>
						<div class="row" style="margin-top: 1em;">
							<p class="pull-left" style="font-size: 1.3em;font-weight: bold;"> <span style="color: #999;font-size: 0.8em;margin-left: 1em;"></span></p>
						</div>
						<div class="row" style="margin-top: 0.5em;">
							<input type="number" name="" id="" value="" class="form-control" placeholder="" v-model="RMBAmount"/>
						</div>
						<div class="row nomargin" style="margin-top: 1em;">
							<input type="radio" class="pull-left" name="itemType" id="objectItem" value="objectItem" v-model="itemType" style="margin-right: 10px;"/>
							<label class="pull-left" for="objectItem"></label>
						</div>
						<div class="row nomargin" style="margin-top: 10px;display: none;" v-show="itemType == 'objectItem'">
							<div class="input-group pull-left" style="width: 100%;">
					            <span class="input-group-addon"><p style="margin-bottom: 0;font-size: 12px;"></p></span>
					            <input type="text" class="form-control" v-model="links">
					        </div>
						</div>
						<div class="row nomargin" style="margin-top: 10px;">
							<input type="radio" class="pull-left" name="itemType" id="fictitiousItem" value="fictitiousItem" v-model="itemType" style="margin-right: 10px;"/>
							<label class="pull-left" for="fictitiousItem"></label>
						</div>
						<div class="row nomargin" style="margin-top: 10px;display: none;" v-show="itemType == 'fictitiousItem'">
							<div class="input-group pull-left" style="width: 100%;">
					            <span class="input-group-addon"><p style="margin-bottom: 0;font-size: 12px;"></p></span>
					            <input type="text" class="form-control" v-model="links">
					        </div>
						</div>
						<div class="row" style="margin-top: 1em;">
							<img v-bind:src="picSrc" style="width: 100%;"/>
						</div>
					</div>
				</div>
			</div>
			
		</div>
	</div>
</template>

<script>
	import {getService} from '../../assets/js/configs.js'
	import notice from './notice.vue'
	export default {
	  name: 'supersede',
	  data(){
	  	return{
	  		userInfo:'',
	  		itemType:'',
	  		links:'',
	  		typeStr:'',
	  		picSrc:'',  //
	  		RMBAmount:'',//
	  		rate:'',//
	  		feeAmount:'',//
	  		totalAmount:'',
	  		comments:'',
	  		created_at:'',
	  		updated_at:'',
	  		billID:'',
	  		dealState:'',
	  		typeState:'',
	  		userLastSix:'', //
	  		userBankName:'', //
	  		centerNoticeTitle:'?',
	  		getCenterNotice:{
	  			showFlag:false,
	  			noticeArray:[]
	  		},
	  	}
	  },
	  components:{
			notice
		},
	  mounted:function () {
	  	this.getUserInfo();
	  	this.getPic();
	  	this.getsupersedeNoticeInfoFunc();
	  },
	  methods:{
	  	getsupersedeNoticeInfoFunc(){
	  		var that=this;
	  		var param = {
		  		"location":''
		  	}
	  		$.ajax({
		  			type:'post',
		  			url:getService()+"/announcement/findCommonAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	console.log('res=',res);
				    	if(res.error_code == 0){
				    		that.getCenterNotice.showFlag=true;
				    		that.getCenterNotice.noticeArray = res.data;
				    	}
				    	
				    },
				    error:function(res){
				    	
				    }
		  		});

	  	},
	  	getUserInfo:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/user/getInfo",
	  			dataType: "json",
	  			async:true,
				headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 500) {
			    		this.userInfo = res.data;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	getPic:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/adv/getDFpage",
	  			dataType: "json",
	  			async:true,
				headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.picSrc = res.data.imageLink;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	showStep2:function () {
	  		$('#step1').fadeOut(1);
	  		$('#step2').fadeIn();
	  	},
	  	showStep3:function () {
	  		$('#step2').fadeOut(1);
	  		$('#step3').fadeIn();
	  		console.log(this.typeStr);
//	  		if (this.typeStr == 'R') {
	  			var param = {
	  				RMBAmount:this.RMBAmount,
	  				rateType:'RcoinRate'
	  			};
	  			$.ajax({
		  			type:'post',
		  			url:getService()+"/getThisUserRate",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		this.rate = res.data.rate;
				    		this.feeAmount = res.data.feeAmount;
				    		this.totalAmount = res.data.totalAmount;
				    	}
				    }.bind(this)
		  		});
//	  		}
//	  		else{
//	  			
//	  		}
	  	},
	  	finalStep:function () {
	  		var isVirtualItem = false;
	  		if (this.itemType == 'fictitiousItem') {
	  			isVirtualItem = true;
	  		}
	  		if (this.typeStr == 'R') {
	  			var param={
		  			"typeStr": this.typeStr,
					  "itemInfo": {
					    "itemLink": this.links
					  },
					  "RMBAmount": this.RMBAmount,
					  "comment": this.comments,
					  "isVirtualItem": isVirtualItem,
					  "paymentInfo": {
					    "friendAlipayAccount": "account@alu.com",
					    "paymentDFAccount": "ACV@GMAIL.com"
					  }
		  		}
		  		$.ajax({
		  			type:'post',
		  			url:getService()+"/addRcoinsBill",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		this.created_at = this.transformDate(res.data.created_at);
				    		this.updated_at = this.transformDate(res.data.updated_at);
				    		this.billID = res.data.billID;
				    		this.dealState = res.data.dealState;
				    		this.typeState = res.data.typeState;
				    	}
				    }.bind(this)
		  		});
	  		} else{
	  			var param={
		  			"typeStr": "",
					  "itemInfo": {
					    "itemLink": this.links
					  },
					  "RMBAmount": this.RMBAmount,
					  "comment": this.comments,
					  "chargeInfo":{"chargeMethod":this.userBankName, "chargeAccount": this.userLastSix,"toOurAccount":this.typeStr.split(' ')[0]},
					  "paymentInfo": {
					    "friendAlipayAccount": "account@alu.com",
					    "paymentDFAccount": "ACV@GMAIL.com"
					  }
		  		}
	  			$.ajax({
		  			type:'post',
		  			url:getService()+"/addAnotherBill",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		this.created_at = this.transformDate(res.data.created_at);
				    		this.updated_at = this.transformDate(res.data.updated_at);
				    		this.billID = res.data.billID;
				    		this.dealState = res.data.dealState;
				    		this.typeState = res.data.typeState;
				    	}
				    }.bind(this)
		  		});
	  		}
	  		
	  	},
	  	//	  	
	  	transformDate:function (timeStamp) {
	  		var date = new Date(timeStamp);//10*1000131000
	        var y = date.getFullYear();    
		    var m = date.getMonth() + 1;    
		    m = m < 10 ? ('0' + m) : m;    
		    var d = date.getDate();    
		    d = d < 10 ? ('0' + d) : d;    
		    var h = date.getHours();  
		    h = h < 10 ? ('0' + h) : h;  
		    var minute = date.getMinutes();  
		    var second = date.getSeconds();  
		    minute = minute < 10 ? ('0' + minute) : minute;    
		    second = second < 10 ? ('0' + second) : second;   
		    return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;  
//	        var str = Y+M+D+h+m+s;
//	        return date;
	  	},
	  	setUserBankInfo:function (userLastSix,userBankName) {
	  		this.userLastSix = userLastSix;
	  		this.userBankName = userBankName;
	  	}
	  }
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
	.table th,td{text-align: center;}
	.noHover:hover{cursor: default;}
</style>


