					<!--<p class="pull-left actionTxt"></p>-->
					<div class="pull-left" style="width: 33%;height: 19px;border-bottom: 1px solid #999;margin: 0 5px;"></div>
					<p class="pull-left unaction">2</p>
					<!--<p class="pull-left actionTxt"></p>-->
					<div class="pull-left" style="width: 33%;height: 19px;border-bottom: 1px solid #999;margin: 0 5px;"></div>
					<p class="pull-left unaction">3</p>
					<!--<p class="pull-left actionTxt"></p>-->
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;">
				<div class="col-sm-12 col-xs-12">
					<p class="pull-left nomargin" style="font-size: 1.1em;color: #333;margin-right: 1em;font-weight: bold;">R</p>
					<p class="pull-left nomargin" style="font-size: 1em;color: #666;margin-top: 0.1em;"><span style="color: #398439;margin-left: 0.5em;">{{rate}}</span></p>
					<p class="pull-left nomargin" style="font-size: 1em;color: #666;margin-top: 0.1em;"><span style="color: #398439;margin-left: 0.5em;margin-right: 0.5em;">$0</span>TWD</p>
				</div>
			</div>
			<div class="row nomargin" style="margin: 0.5em 0;">
				<div class="col-sm-12 col-xs-12 nopadding">
					<input type="number" name="" id="" value="" class="form-control" style="box-shadow: none;-webkit-box-shadow: none;border-left: none;border-right: none;border-radius: 0;" placeholder=""/>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;">
				<div class="col-sm-12 col-xs-12">
					<p class="pull-left nomargin" style="font-size: 1.1em;color: #333;font-weight: bold;"></p>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 0.5em;">
				<div class="col-sm-12 col-xs-12">
					<p class="pull-left nomargin" style="font-size: 1em;color: #333;">ATM/ATM<span style="font-size: 0.8em;color: #999;"></span></p>
				</div>
			</div>
			<div class="row nomargin">
				<div class="col-sm-12 col-xs-12 nopadding" style="border: 0.05em solid #ccc;border-left: none;border-right: none;">
					<input type="radio" class="pull-left" name="" id="" value="" style="margin: 1em;"/>
					<img src="../../assets/images/mobile/guai.png" style="width: 2em;margin-top: 0.5em;margin-right: 1em;" class="pull-left"/>
					<p class="pull-left" style="font-size: 1em;margin-top: 0.8em;">700   47936</p>
				</div>
			</div>
			<div class="row nomargin">
				<div class="col-sm-12 col-xs-12 nopadding" style="border: 0.05em solid #ccc;border-left: none;border-right: none;">
					<input type="radio" class="pull-left" name="" id="" value="" style="margin: 1em;"/>
					<img src="../../assets/images/mobile/Bitmap.png" style="width: 2em;margin-top: 0.5em;margin-right: 1em;" class="pull-left"/>
					<p class="pull-left" style="font-size: 1em;margin-top: 0.8em;">808   175242</p>
				</div>
			</div>
			<div class="row nomargin">
				<div class="col-sm-12 col-xs-12 nopadding" style="border: 0.05em solid #ccc;border-left: none;border-right: none;">
					<div style="background: rgba(0,0,0,0.5);height: 3em;position: absolute;width: 100%;z-index: 99;" v-if="!userInfo.isCStoreOpened">
						<center><p style="color: #fff;line-height: 3em;">2%3030</p></center>		
					</div>
					<input type="radio" class="pull-left" name="" id="" value="" style="margin: 1em;"/>
					<img src="../../assets/images/mobile/chaoshangfukuan.png" style="width: 2em;margin-top: 0.5em;margin-right: 1em;" class="pull-left"/>
					<p class="pull-left" style="font-size: 1em;margin-top: 0.8em;">711//OK</p>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;">
				<div class="col-sm-12 col-xs-12">
					<center><input type="checkbox" name="" id="" value="" style="margin-right: 0.5em;margin-top: 0.2em;"/><a href="javascript:void(0)"></a></center>
				</div>
			</div>
			<div class="row nomargin" style="margin-top: 1em;margin-bottom: 6em;">
				<div class="col-sm-12 col-xs-12 nopadding" style="border: 0.05em solid #ccc;border-left: none;border-right: none;">
					<p class="pull-left nomargin" style="margin-left: 2em;line-height: 4em;">99999</p>
					<button type="button" class="btn btn-warning pull-right" style="margin-right: 2em;margin-top: 1em;background: #ff8500;"></button>
				</div>
			</div>
			<div class="row nomargin" style="position: fixed;bottom: 0;background: #fff;width: 100%;border-top: 0.05em solid #ccc;padding-top: 0.5em;">
				<a href="">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/shouyeicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;"></p></center>
					</div>
				</a>
				<a href="">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/daifuicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;"></p></center>
					</div>
				</a>
				<a href="/mobileEntry">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/daigouicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;"></p></center>
					</div>
				</a>
				<a href="">
					<div class="col-xs-3 col-sm-3 nopadding">
						<center><img src="../../assets/images/mobile/huiyuanicon.png" style="width: 2em;"/></center>
						<center><p style="font-size: 0.8em;color: #333;margin-bottom: 0.5em;"></p></center>
					</div>
				</a>
			</div>
		</div>
	</div>
</template>

<script>
	import {getService} from '../../assets/js/configs.js'
	export default {
		name: 'RBrecharge',
		data (){
	  	return{
	  		userInfo:'',
	  		typeStr:'',
	  		rate:'',
	  		comments:'',
	  		feeAmount:'',//
	  		feeRate:'',
	  		totalAmount:'',
	  		RMBAmount:'',
	  		userLastSix:'',
	  		userBankName:'',
	  		created_at:'',
	  		updated_at:'',
	  		billID:'',
	  	}
	  },
	  watch:{
	  	RMBAmount:function(val,oldVal){
	  		console.log('---val=',val);
	  		this.getRate(val);
	  	},

	  },
	  mounted:function (){
	  	this.getInfo();
	  	//this.getRate();
	  },
	  methods:{
	  	getInfo:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/user/getInfo",
	  			dataType: "json",
	  			data:{
	  				
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0 || res.error_code==200) {
			    		this.userInfo = res.data;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	getRate:function (num) {
	  		var that=this;
	  		var param={
	  			"RMBAmount":num,
   				"rateType":"RcoinRate"
	  		};
	  		$.ajax({
	  			type:'post',
	  			url:getService()+"/getThisUserRate",
	  			dataType: "json",
	  			data:JSON.stringify(param),
	  			async:true,
	  			headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
	  				withCredentials: true
	  			},
	  			crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 200 || res.error_code == 0) {
			    		that.rate = (res.data.rate/100).toFixed(2);
			    		that.feeAmount = res.data.feeAmount;
			    		that.feeRate = res.data.feeRate;
			    		that.totalAmount = res.data.totalAmount;
			    	}
			    }
	  		});
	  	},
	  	setUserBankInfo:function (userLastSix,userBankName) {
	  		this.userLastSix = userLastSix;
	  		this.userBankName = userBankName;
	  	},
	  	nextStep:function () {
	  		$('#step1').hide();
	  		$('#step2').show();
	  		var param = {
  				RMBAmount:this.RMBAmount,
  				rateType:'RcoinRate'
  			};
  			$.ajax({
	  			type:'post',
	  			url:getService()+"/getThisUserRate",
	  			dataType: "json",
	  			data:JSON.stringify(param),
	  			async:true,
				headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.rate = res.data.rate;
			    		this.feeAmount = res.data.feeAmount;
			    		this.totalAmount = res.data.totalAmount;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	//	  	
	  	transformDate:function (timeStamp) {
	  		var date = new Date(timeStamp);//10*1000131000
	        var y = date.getFullYear();    
		    var m = date.getMonth() + 1;    
		    m = m < 10 ? ('0' + m) : m;    
		    var d = date.getDate();    
		    d = d < 10 ? ('0' + d) : d;    
		    var h = date.getHours();  
		    h = h < 10 ? ('0' + h) : h;  
		    var minute = date.getMinutes();  
		    var second = date.getSeconds();  
		    minute = minute < 10 ? ('0' + minute) : minute;    
		    second = second < 10 ? ('0' + second) : second;   
		    return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;  
//	        var str = Y+M+D+h+m+s;
//	        return date;
	  	},
	  	finalStep:function () {
	  		var param = {
	  			RMBAmount:this.RMBAmount,
				comment:this.comments,
				chargeInfo:{chargeMethod:this.userBankName, chargeAccount: this.userLastSix,toOurAccount:this.typeStr.split(' ')[0]}
	  		}
	  		$.ajax({
	  			type:'post',
	  			url:getService()+"/addRcoinChargeBill",
	  			dataType: "json",
	  			data:JSON.stringify(param),
	  			async:true,
				headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.billID = res.error_msg.billID;
			    		this.created_at = this.transformDate(res.error_msg.created_at);
				    	this.updated_at = this.transformDate(res.error_msg.updated_at);
				    	$('#step2').hide();
				    	$('#step3').show();
			    	}
			    }.bind(this)
	  		});
	  	}
	  }
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
</style>


