<script>
	import {getService} from '../../assets/js/configs.js'
	import notice from './notice.vue'
	export default {
	  name: 'purchase',
	  data (){
	  	return{
	  		userInfo:'',
	  		rate:'',
	  		RMBAmount:'',
	  		feeAmount:'',
	  		totalAmount:'',
	  		created_at:'',
	  		updated_at:'',
	  		links:'',
	  		comments:'',
	  		typeStr:'',
	  		billID:'',
	  		dealState:'',
	  		typeState:'',
	  		userLastSix:'', //
	  		userBankName:'', //
	  		finishFlag:false,
	  		centerNoticeTitle:'?',
	  		getCenterNotice:{
	  			showFlag:false,
	  			noticeArray:[]
	  		},

	  	}
	  },
	  mounted:function (){
	  	this.getInfo();
	  	this.getRate();
	  	this.getPurchaseNoticeInfoFunc();
	  },
	  components:{
			notice
		},
	  methods:{
	  	getPurchaseNoticeInfoFunc(){
	  		var that=this;
	  		var param = {
		  		"location":''
		  	}
	  		$.ajax({
		  			type:'post',
		  			url:getService()+"/announcement/findCommonAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	console.log('res=',res);
				    	if(res.error_code == 0){
				    		that.getCenterNotice.showFlag=true;
				    		that.getCenterNotice.noticeArray = res.data;
				    	}
				    	
				    },
				    error:function(res){
				    	
				    }
		  		});

	  	},
	  	getInfo:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/user/getInfo",
	  			dataType: "json",
	  			data:{
	  				
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 500) {
			    		this.userInfo = res.data;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	getRate:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/getBaseRate",
	  			dataType: "json",
	  			data:{
	  				
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 200) {
			    		this.rate = res.data.detailRate/100;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	setUserBankInfo:function (userLastSix,userBankName) {
	  		this.userLastSix = userLastSix;
	  		this.userBankName = userBankName;
	  	},
	  	//	  	
	  	transformDate:function (timeStamp) {
	  		var date = new Date(timeStamp);//10*1000131000
	        var y = date.getFullYear();    
		    var m = date.getMonth() + 1;    
		    m = m < 10 ? ('0' + m) : m;    
		    var d = date.getDate();    
		    d = d < 10 ? ('0' + d) : d;    
		    var h = date.getHours();  
		    h = h < 10 ? ('0' + h) : h;  
		    var minute = date.getMinutes();  
		    var second = date.getSeconds();  
		    minute = minute < 10 ? ('0' + minute) : minute;    
		    second = second < 10 ? ('0' + second) : second;   
		    return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;  
//	        var str = Y+M+D+h+m+s;
//	        return date;
	  	},
	  	submitInfo:function () {
	  		var param = {
  				RMBAmount:this.RMBAmount,
  				rateType:'RcoinRate'
  			};
  			$.ajax({
	  			type:'post',
	  			url:getService()+"/getThisUserRate",
	  			dataType: "json",
	  			data:JSON.stringify(param),
	  			async:true,
				headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.rate = res.data.rate;
			    		this.feeAmount = res.data.feeAmount;
			    		this.totalAmount = res.data.totalAmount;
			    	}
			    }.bind(this)
	  		});
	  		if(this.typeStr == 'R'){
	  			var param = {
		  			"typeStr":this.typeStr,
					"itemInfo":{"itemLink":this.links},
					"RMBAmount":this.RMBAmount,
					"comment":this.comments
		  		};
		  		$.ajax({
		  			type:'post',
		  			url:getService()+"/addRcoinsBill",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		this.finishFlag = true;
				    		this.created_at = this.transformDate(res.data.created_at);
				    		this.updated_at = this.transformDate(res.data.updated_at);
				    		this.billID = res.data.billID;
				    		this.dealState = res.data.dealState;
				    		this.typeState = res.data.typeState;
				    	}
				    }.bind(this)
		  		});
	  		}
	  		else{
	  			var param = {
		  			"typeStr":"",
					"itemInfo":{"itemLink":this.links},
					"RMBAmount":this.RMBAmount,
					"comment":this.comments,
					"chargeInfo":{"chargeMethod":this.userBankName, "chargeAccount": this.userLastSix,"toOurAccount":this.typeStr.split(' ')[0]}
		  		};
		  		$.ajax({
		  			type:'post',
		  			url:getService()+"/addAnotherBill",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		this.finishFlag = true;
				    		this.created_at = this.transformDate(res.data.created_at);
				    		this.updated_at = this.transformDate(res.data.updated_at);
				    		this.billID = res.data.billID;
				    		this.dealState = res.data.dealState;
				    		this.typeState = res.data.typeState;
				    	}
				    }.bind(this)
		  		});
	  		}
	  	}
	  }
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
	input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }
    input[type="number"]{
        -moz-appearance: textfield;
    }
</style>


